<template>
	<div id="app">
		<loading :show='show' v-on:listenToChildEvent="showMsgFromChild" v-show="isNewPage"></loading>
		<hello v-show="index==0" v-on:listenToChildEvent="showMsgFromChild" :isShowAudio="showAudio"></hello>
		<cqb v-show="index==4" v-on:listenToChildEvent="showMsgFromChild"></cqb>
		<dsj v-show="index==5" v-on:listenToChildEvent="showMsgFromChild"></dsj>
		<jwl v-show="index==2" v-on:listenToChildEvent="showMsgFromChild"></jwl>
		<detail v-show="index==1||!show" :index='index' v-on:listenToChildEvent="showMsgFromChild"></detail>
		<!--<router-view></router-view>-->
	</div>
</template>

<script>
	import hello from '@/components/HelloWorld'
	import loading from '@/components/loading'
	import cqb from '@/components/cqb'
	import dsj from '@/components/dsj'
	import jwl from '@/components/jwl'
	import detail from '@/components/detail'
	export default {
		name: 'app',
		data() {
			return {
				index: 0,
				count : 0,
        show : false,
				showAudio : false,
        isNewPage : true
			}
		},
		components: {
			hello,
			loading,
			cqb,
			dsj,
			jwl,
			detail,

		},
    created(){
      if(window.sessionStorage.getItem('isnewpage')){
        this.isNewPage = false
      }
    },
		mounted () {
            var _this = this
            let imgs = document.querySelectorAll('img')
//          console.log(imgs)
            Array.from(imgs).forEach((item)=>{
                let img = new Image()
                img.onload = ()=>{
                    this.count++
                }
                img.src=item.getAttribute('src')
            })
        },
    methods:{
    	showMsgFromChild : function(data){
        console.log(data)
        if(data == 9){
          this.showAudio = true
        }else{
          this.index = data
          if(data == 0){
						// alert(window.sessionStorage.getItem('headerheight'))
						console.log(window.sessionStorage.getItem('headerheight'))
            $('body,html').animate({ scrollTop: window.sessionStorage.getItem('headerheight')},0);
          }else{
            $('body,html').animate({ scrollTop: 0 },0);
          }

        }
      }
    },
    watch : {
	        count (val,oldval) {
	            if(val == 74){
	                this.show = true
	                //然后可以对后台发送一些ajax操作
	            }
	        }
        }
	}
</script>



